import React from 'react';
import {Link} from 'react-router-dom';
import imgShoulderPull from '../assets/shoulder-pull.webp';
import imgShoulderPull2x from '../assets/shoulder-pull-2x.webp';
import CircleW from '../assets/circle-white.svg';
import Carousel from '../components/carousel';

const Home = () => {
  return (
    <div className="font-main">
      <div className="border-zinc-400 border-l-2 h-14 absolute left-1/2 -ml-px my-12"></div>
      <section className="container text-ellipsis pt-44 pb-16">
        <div className="text-p-size max-w-[75ch]">
          <div className="md:border-l-2 md:border-zinc-400 px-10 md:px-0 md:pl-20 md:ml-12 mb-24 pb-12">
            <h2 className="text-h1-size font-medium">Cze, jestem Natalia.</h2>
            <br />
            <p>
              Moj misj jest promowanie aktywnoci fizycznej oraz budowanie
              wiadomoci ciaa  tego jak z nim pracowa, aby mie wicej
              energii i sta si zdrowszym, sprawniejszym oraz efektywniejszym
              czowiekiem.
            </p>
            <br />
            <img
              src={imgShoulderPull}
              scrset={`${imgShoulderPull} 1x, ${imgShoulderPull2x} 2x,`}
              className="rounded-small-images my-8"
            ></img>
            <p className="pt-8 pb-4">
              Kadego dnia widz jak ruch zmienia ycie moich podopiecznych na
              lepsze. Ogromn satysfakcj daje mi obserwowanie tego, jak efekty
              wsplnej pracy sigaj daleko poza sal treningow czy sam form
              fizyczn.
            </p>
            <p>
              Dla mnie odkd pamitam, ruch by nieodczn czci ycia 
              pomaga pozby si negatywnych emocji, zadba o ciao i osign
              lepsze wyniki w rnych dyscyplinach.
            </p>
          </div>
        </div>
      </section>

      <div className="h-96 w-full bg-white">
        <div className="container">
          <Link to="/oferta">
            <button
              type="button"
              className="rounded-button bg-black text-center font-medium text-white text-p-size absolute h-20 w-96 left-1/2 -ml-48 m-16"
            >
              {' '}
              zobacz ofert {'>>'}
            </button>
          </Link>
          <div className="border-zinc-400 border-l-2 h-36 absolute left-1/2 -ml-px my-auto mt-48"></div>
        </div>
      </div>

      <section className="container text-ellipsis py-16 md:pl-20">
        <details className="text-p-size max-w-prose">
          <summary className="list-none flex flex-col md:flex-row md:items-baseline">
            <h1 className="text-h1-size font-medium text-center md:text-left pb-6 md:pb-0">
              Certyfikaty
            </h1>
            <img src={CircleW} className="inline md:pr-6 flex-grow-0 self-center md:order-first"></img>
          </summary>
          <br />
          <p>
            Dowiadczenie trenerskie zdobywaam pracujc na siowniach oraz w
            klubach sportowych, opiekujc si osobami trenujcymi zawodowo, jak
            i amatorsko oraz ludmi, ktrzy po prostu chcieli si porusza.
            Ukoczyam wiele kursw, szkole i konferencji z zakresu treningu
            personalnego, przygotowania motorycznego oraz dietetyki, ktre day
            mi solidn podstaw do pracy z rnorodnymi podopiecznymi. Cay czas
            si ucz, doksztacam i poszerzam swoj wiedz. Kilka wybranych
            certyfikatw znajdziesz poniej.
          </p>
          <div className="py-12">
            <Carousel />
          </div>
        </details>
      </section>

      <div className="bg-white">
        <section className="container py-16 relative md:pl-20">
          <details className="text-p-size max-w-prose">
            <summary className="list-none flex flex-col md:flex-row md:items-baseline">
              <h1 className="text-h1-size font-medium text-center md:text-left pb-6 md:pb-0">
                Opinie
              </h1>
              <img src={CircleW} className="inline md:pr-6 flex-grow-0 self-center md:order-first"></img>
            </summary>
            <section className="text-small-size rounded-standard my-14 bg-content-background shadow-xl p-8">
              <h2 className="font-bold mb-4">Karolina</h2>
              <blockquote className="max-w-prose italic">
                - Z kadej kontuzji, zmieni nastawienie do treningu - teraz to
                sama przyjemno a nie cige zmuszanie si. A co najwaniejsze
                - potrafi tak zakorzeni sport we krwi, e ju nigdy nie ma si
                do! Polecam z caego serduszka.
              </blockquote>
            </section>
            <section className="text-small-size rounded-standard my-14 bg-content-background shadow-xl p-8">
              <h2 className="font-bold mb-4">Micha</h2>
              <blockquote className="max-w-prose italic">
                - Polecam Natali z caego serca. Peen profesjonalizm i ogromna
                wiedza i dowiadczenie w pracy co przekada si na efekty. Praca
                z Natalia to nie tylko niezy wycisk, ale te mio spdzony
                czas. Jeeli kto szuka trenera to szczerze polecam -
                odpowiednia osoba na odpowiednim miejscu.
              </blockquote>
            </section>
            <section className="text-small-size rounded-standard my-14 bg-content-background shadow-xl p-8">
              <h2 className="font-bold mb-4">Agnieszka</h2>
              <blockquote className="max-w-prose italic">
                - Profesjonalne treningi z Natali w kameralnym miejscu to moja
                ulubiona cz dnia! Trenuj z Natali ju pewien czas i wida
                efekty! wietny kontakt, peen profesjonalizm i due
                dowiadczenie czyni Natali REWELACYJN trenerk!!! Polecam z
                caego serca, jeeli chcesz zmieni swoje ycie na lepsze!!! :)
              </blockquote>
            </section>
            <section className="text-small-size rounded-standard my-14 bg-content-background shadow-xl p-8">
              <h2 className="font-bold mb-4">Katarzyna</h2>
              <blockquote className="max-w-prose italic">
                - Natalia to prawdziwa pasjonatka. Jest profesjonalistk w
                kadym calu. Cierpliwa, ale jednoczenie bardzo wymagajca. Nie
                odpuszcza. Trening z Ni to cika praca, przynoszca oczekiwane
                rezultaty. Poza tym to bardzo mia, wartociowa osoba a jej
                umiech agodzi najwikszy bl. Polecam.
              </blockquote>
            </section>
            <section className="text-small-size rounded-standard my-14 bg-content-background shadow-xl p-8">
              <h2 className="font-bold mb-4">Anna</h2>
              <blockquote className="max-w-prose italic">
                - Trening z Natali to profesjonalnie uoony plan treningowy -
                wiem po co i nad czym naley pracowa - czasem jest wycisk, ale
                w takiej atmosferze z przyjemnoci czowiek si mczy :)
                Polecam!!!
              </blockquote>
            </section>
          </details>
        </section>
      </div>
    </div>
  );
};

export default Home;

