import React from 'react';
import {BrowserRouter as Router, Routes, Route} from 'react-router-dom';
import Header from './components/header';
import Home from './pages/home';
import Oferta from './pages/oferta';
import Cennik from './pages/cennik';
import Kontakt from './pages/kontakt';
import Footer from './components/footer';

const menuMargin = React.createContext(null);

const App = props => {
  return (
    <div className="antialiased bg-content-background min-h-screen flex flex-col overflow-hidden relative min-w-[560px]">
      <React.StrictMode>
        <Router basename="/natalia-dziubek" >
          <Header />

          <Routes>
            <Route index element={<Home />} />

            <Route
              path="/home"
              element={
                <React.Suspense fallback={<>adowanie...</>}>
                  <Home />
                </React.Suspense>
              }
            />

            <Route
              path="/oferta"
              element={
                <React.Suspense fallback={<>adowanie...</>}>
                  <Oferta />
                </React.Suspense>
              }
            />
            <Route
              path="/cennik"
              element={
                <React.Suspense fallback={<>adowanie...</>}>
                  <Cennik />
                </React.Suspense>
              }
            />
            <Route
              path="/kontakt"
              element={
                <React.Suspense fallback={<>adowanie...</>}>
                  <Kontakt />
                </React.Suspense>
              }
            />
            <Route
              path="*"
              element={
                <React.Suspense fallback={<>adowanie...</>}>
                  <Home />
                </React.Suspense>
              }
            />
          </Routes>
        </Router>
        <Footer />
      </React.StrictMode >
    </div>
  );
};

export default App;
