import React, {useState, useRef, useEffect} from 'react';
import {Link} from 'react-router-dom';

export default function Navbar() {
  const [isNavOpen, setIsNavOpen] = useState(false);

  return (
    <nav
      className="
    fixed
    mt-0
    justify-between
    w-full
    md:w-2/3
    2xl:w-3/5
    p-4
    md:p-0
    text-lg
    z-40"
    >
      <button
        className={`float-right h-16 w-16 text-white cursor-pointer md:hidden block mr-auto ${
          isNavOpen ? 'hidden' : 'block'
        } `}
        type="button"
        onClick={() => {
          setIsNavOpen(prev => !prev);
        }}
      >
        <svg
          xmlns="<http://www.w3.org/2000/svg>"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth="2"
            d="M4 6h16M4 12h16M4 18h16"
          />
        </svg>
      </button>
      <div
        className={`md:hidden flex flex-col items-stretch w-full ${
          isNavOpen ? 'block' : 'hidden'
        }`}
      >
        <button onClick={() => setIsNavOpen(false)} type="button">
          <svg
            className="float-right h-16 w-16 text-white"
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
          >
            <line x1="18" y1="6" x2="6" y2="18" />
            <line x1="6" y1="6" x2="18" y2="18" />
          </svg>
        </button>
        <ul className="float-right min-h-[250px] flex flex-col flex-1 justify-evenly bg-secondary">
          <li className="">
            <Link to="/home">o mnie</Link>
          </li>
          <li className="">
            <Link to="/oferta">oferta</Link>
          </li>
          <li className="">
            <Link to="/cennik">cennik</Link>
          </li>
          <li className="">
            <Link to="/kontakt">kontakt</Link>
          </li>
        </ul>
      </div>

      <div
        className="
      w-full h-[5vh]
      min-h-[3.125rem]
      hidden md:flex
      md:items-center
      justify-between
      bg-nav text-white
      rounded-navbar"
      >
        <ul className="pt-4 md:flex md:pt-0 mx-auto">
          <li className="md:p-4 py-3 block font-main text-nav-size hover:text-rose-600">
            <Link to="/home">o mnie</Link>
          </li>
          <li className="md:p-4 py-3 block font-main text-nav-size hover:text-rose-600">
            <Link to="/oferta">oferta</Link>
          </li>
          <li className="md:p-4 py-3 block font-main text-nav-size hover:text-rose-600">
            <Link to="/cennik">cennik</Link>
          </li>
          <li className="md:p-4 py-3 block font-main text-nav-size hover:text-rose-600">
            <Link to="/kontakt">kontakt</Link>
          </li>
        </ul>
      </div>
    </nav>
  );
}
