import React from 'react';
import Caption from './caption';
import heroHomeDesktop from '../assets/hero-home-d.webp';
import heroHomeMobile from '../assets/hero-home-m.webp';
import imgInsta from '../assets/insta.svg';
import imgFacebook from '../assets/fb.svg';
import imgIn from '../assets/linkedin.svg';

const Hero = () => {
  return (
    <div className="bg-[url('../assets/hero-home-m.webp')] md:bg-none bg-primary bg-right-top">
      <div className="md:container flex">
        <div className="min-h-[70vh] md:min-h-0">
        <Caption />
        </div>
        <picture className="hidden md:block flex-auto">
          <source
            type="image/webp"
            media="(min-width:768px)"
            srcSet={heroHomeDesktop}
          />
          <img
            className="md:mt-navbar-gap w-full md:w-auto object-cover md:object-scale-down md:ml-[45%] mdlg:ml-[50%] min-h-[12rem] max-h-[60vh] relative md:rounded-hero-desktop z-10"
            src={heroHomeMobile}
            alt="Natalia Dziubek"
          ></img>
        </picture>

        <div className="z-10 mt-navbar-gap pt-10 text-white absolute mdlg:right-2 lg:right-10">
          <a
            className="hidden mdlg:block mdlg:mt-14 py-2"
            href="https://instagram.com"
          >
            <img className="px-2" src={imgInsta}></img>
          </a>
          <a className="hidden mdlg:block py-2" href="https://facebook.com">
            <img className="px-2" src={imgFacebook}></img>
          </a>
          <a className="hidden mdlg:block py-2" href="https://linkedin.com">
            <img className="px-2" src={imgIn}></img>
          </a>
        </div>

        <div className="hidden md:block bg-secondary md:rounded-dark-desktop h-full w-[35vw] md:w-[44vw] top-0 right-0 absolute z-0 overflow-hidden"></div>
      </div>
    </div>
  );
};

export default Hero;

