import React from 'react';
import {Formik, Field, Form} from 'formik';

const ContactForm = () => {
  return (
    <div>
      <Formik
        initialValues={{
          firstName: '',
          lastName: '',
          email: '',
        }}
        onSubmit={async values => {
          await new Promise(resolve => setTimeout(resolve, 500));
          alert(JSON.stringify(values, null, 2));
        }}
      >
        <Form className="flex flex-col">
          <Field
            id="firstName"
            className="bg-white rounded-contact-field h-11 w-1/2 p-2 my-4 shadow-md"
            name="firstName"
            maxlength={255}
            placeholder="twoje imi"
            type="text"
          />
          <Field
            id="email"
            className="bg-white rounded-contact-field h-11 w-1/2 p-2 my-4 shadow-md"
            name="email"
            maxlength={255}
            placeholder="twj e-mail"
            type="email"
          />
          <Field
            id="message"
            as="textarea"
            wrap={'hard'}
            maxlength={2500}
            spellcheck={true}
            className="bg-white rounded-contact-field h-96 w-auto p-2 my-4 shadow-md break-words resize-none relative"
            name="message"
            placeholder="twoja wiadomo"
          />
          <button
            type="submit"
            className="rounded-submit bg-black text-center font-medium text-white text-p-size h-10 w-32 my-4 mr-auto ml-auto"
          >
            Wylij!
          </button>
        </Form>
      </Formik>
    </div>
  );
};

export default ContactForm;
