import React from 'react';
import Arrow from '../assets/arrow.svg';

const Cennik = () => {
  return (
    <div className="container font-main">
      <div className="border-zinc-400 border-l-2 h-14 absolute left-1/2 -ml-px mt-12"></div>
      <div className="px-10 md:px-0 md:pl-20 md:ml-12 mb-24 pb-12 mt-44">
        <h1 className="text-h1-size pb-8 font-medium">Cennik</h1>
        <section className="px-8">
          <h2 className="text-h2-size py-4 font-medium inline">
            <img
              src={Arrow}
              aria-hidden="true"
              className="inline whitespace-nowrap pr-4"
            />
            Pojedyczy trening personalny -{' '}
            <span className="text-lime-600">169z</span>
          </h2>
          <h2 className="text-h2-size pt-8 pb-4 font-medium">
            <img
              src={Arrow}
              aria-hidden="true"
              className="inline whitespace-nowrap pr-4"
            />
            Pakiety miesiczne:
          </h2>
          <ul className="list-disc ml-28 text-offer-size">
            <li className="pb-1">
              pakiet 4 treningw -
              <span className="text-lime-600 font-medium">649z</span>
            </li>
            <li className="pb-1">
              pakiet 8 treningw -{'  '}
              <span className="text-lime-600 font-medium">1040z</span>
            </li>
            <li>
              pakiet 12 treningw -{'  '}
              <span className="text-lime-600 font-medium">1440z</span>
            </li>
          </ul>
          <h2 className="text-h2-size pt-8 pb-4 font-medium">
            <img
              src={Arrow}
              aria-hidden="true"
              className="inline whitespace-nowrap pr-4"
            />
            Trening dla dwch osb:
          </h2>
          <ul className="list-disc ml-28 text-offer-size">
            <li className="pb-1">
              pojedyczy trening -{'  '}
              <span className="text-lime-600 font-medium">200z</span>
            </li>
            <li>
              pakiet 8 treningw -{'  '}
              <span className="text-lime-600 font-medium">1200z</span>
            </li>
          </ul>
        </section>
      </div>
    </div>
  );
};

export default Cennik;
