import React, {Component} from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import Img1 from '../assets/certificates/1.webp';
import Img2 from '../assets/certificates/2.webp';
import Img3 from '../assets/certificates/3.webp';
import Img4 from '../assets/certificates/4.webp';
import Img5 from '../assets/certificates/5.webp';
import Img6 from '../assets/certificates/6.webp';

const Carousel = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };
  return (
    <Slider {...settings}>
      <div>
        <img className="block mx-auto" src={Img6}></img>
      </div>
      <div>
        <img className="block mx-auto" src={Img2}></img>
      </div>
      <div>
        <img className="block mx-auto" src={Img3}></img>
      </div>
      <div>
        <img className="block mx-auto" src={Img4}></img>
      </div>
      <div>
        <img className="block mx-auto" src={Img5}></img>
      </div>
      <div>
        <img className="block mx-auto" src={Img1}></img>
      </div>
    </Slider>
  );
};

export default Carousel;
